<template>
    <p-data-table
        :value="completeList"
        v-model:selection="selectedList"
        selection
        dataKey="cycleId"
        class="p-datatable-sm"
        showGridlines
        responsiveLayout="scroll"
        :loading="loading"
        :scrollable="true"
        scrollHeight="calc(100vh - 130px)"
    >
        <template #header>
            <div class="table-header" style="display: flex;">
                <p-checkbox
                    v-model="uploaded"
                    :binary="true"
                    class="p-mr-2 p-mb-2"
                    style="margin-top: 5px;"
                />
                <label class="p-mr-4" style="margin-top: 8px;">Uploaded</label>

                <label class="p-mr-2 p-mt-2">Filter</label>

                <p-multi-select
                    v-model="sourceFilter"
                    :options="sourceFilterOpts"
                    optionLabel="label"
                    optionValue="value"
                    class="p-mr-2"
                    style="width: 200px;"
                />

                <p-button
                    label="Filter"
                    class="p-mr-2 p-button-sm"
                    @click="filter"
                />
                <p-button
                    label="Download"
                    class="p-mr-2 p-button-sm p-button-warning"
                    @click="download"
                />
                <p-button
                    label="Mark As Upload"
                    class="p-mr-2 p-button-sm p-button-success"
                    @click="markAsUploadHandler"
                />
            </div>
        </template>

        <p-column
            selectionMode="multiple"
            headerStyle="width: 3em"
            style="width: 3rem; max-width: 3rem;"
        />
        <p-column field="patientName" header="Patient name" />
        <p-column
            field="patientId"
            header="Chart ID"
            style="max-width: 12rem"
        />
        <p-column
            field="dateCompleted"
            header="Date Completed"
            style="max-width: 12rem"
        />
        <p-column
            field="cycleNo"
            header="Cycle Number"
            style="max-width: 10rem"
        />
        <p-column field="cycleId" header="Cycle id" style="max-width: 7rem" />
        <p-column
            field="cycleName"
            header="Cycle Type"
            style="max-width: 8rem"
        />
        <p-column
            field="cycleNoType"
            header="Cycle no type"
            style="max-width: 8rem"
        />
        <p-column field="ohip" header="Funded" style="max-width: 7rem">
            <template #body="{ data }">
                {{ data.ohip == 1 ? "Yes" : "No" }}
            </template>
        </p-column>
        <p-column
            field="cycleDate"
            header="Cycle Date"
            style="max-width: 120px"
        >
            <template #body="{ data }">
                {{ data.cycleDate.split(" ")[0] }}
            </template>
        </p-column>

        <p-column field="source" header="Source" />

        <p-column></p-column>

        <template #footer>
            <p-paginator
                :rows="10"
                :totalRecords="totalRecords"
                :rowsPerPageOptions="[10, 20, 30]"
                @page="onPage($event)"
            />
        </template>
    </p-data-table>
</template>

<script>
import { ORIGIN } from "@/config";
import {
    getBornCompleteList,
    getBornCSV,
    getBronCsvMulti,
    markAsUpload
} from "@/service/art-cycle";
import { ref, onMounted } from "vue";
import { useStore } from "vuex";

export default {
    setup() {
        const completeList = ref([]);
        const selectedList = ref([]);
        const loading = ref(false);
        const totalRecords = ref(0);
        const uploaded = ref(false);
        const page = ref(1);
        const perPage = ref(10);
        const sourceFilter = ref([]);
        const sourceFilterOpts = ref([
            { value: "cycleMonitoring", label: "Cycle monitoring" },
            { value: "opu", label: "Opu" },
            { value: "pregnancy", label: "Pregnancy" },
            { value: "fertilization", label: "Fertilization" },
            { value: "embryoTransfer", label: "Embryo transfer" }
        ]);

        const getCompleteList = async () => {
            loading.value = true;
            const { data, total } = await getBornCompleteList(
                page.value,
                perPage.value,
                uploaded.value ? 1 : 0,
                sourceFilter.value
            );
            completeList.value = data;
            totalRecords.value = total;
            loading.value = false;
            selectedList.value = [];
        };

        const onPage = (event) => {
            page.value = event.page + 1;
            perPage.value = event.rows;
            getCompleteList();
        };

        const cyclePayload = () => {
            return selectedList.value.map((item) => ({
                cycleId: item.cycleId,
                patientId: item.patientId
            }));
        };

        const store = useStore();
        const download = async () => {
            const payload = {
                cycle: cyclePayload()
            };

            const resp = await getBronCsvMulti(payload);
            if (resp.status == "failed") return;

            window.open(`${ORIGIN}/${resp.csv}`);
            selectedList.value = [];
        };

        const markAsUploadHandler = async () => {
            const payload = {
                cycle: cyclePayload(),
                status: 1
            };
            await markAsUpload(payload);
            getCompleteList();
        };

        onMounted(() => {
            getCompleteList();
        });

        return {
            completeList,
            selectedList,
            loading,
            uploaded,
            totalRecords,
            onPage,
            download,
            markAsUploadHandler,
            filter: () => {
                getCompleteList();
            },
            sourceFilter,
            sourceFilterOpts
        };
    }
};
</script>
